<template>
    <yd-layout>
        <div class="listContent">
            <div class="news_date">
                <p class="date_p">2017年3月13日</p>
            </div>
        </div>
        <div class="listContent">
            <div class="listWidth">
                <yd-list theme="5">
                    <yd-list-item type="div" v-for="(item, index) in list" :key="index">
                        <span slot="title" class="span_title span_logo fontColor" v-text="item.title">
                        </span>
                        <div slot="other">
                            <yd-cell-group>
                                <yd-cell-item arrow>
                                </yd-cell-item>
                            </yd-cell-group>
                        </div>
                        <yd-list-other slot="other">
                            <div>
                                <span class="span_publisher">发布者：张三</span>
                                <span class="span_date">{{formatDate(item.createtime, 'yyyy-MM-dd')}}</span>
                            </div>
                        </yd-list-other>
                    </yd-list-item>
                </yd-list>
            </div>
        </div>
        <div class="more_news">
            <div class="click_more">
                <a class="more_a">查看更多历史公告</a>
            </div>
        </div>
    </yd-layout>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: 'news',
    data() {
        return {
            list: []
        }
    },
    mounted() {
        document.setTitle('宣贯列表');
        this.getList();
    },
    computed: {
        ...mapGetters([
            'getUserInfo'
        ])
    },
    methods: {
        getList() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/AddressBook/affiche/list', {
                name: vm.getUserInfo.ssotoken,
                channelId: 1545,
                isParent: 1,
                pageSize: 10
            }, function (res) {
                vm.list = res.jsonData;
                console.log(vm.list);
            });
        }
    }
}
</script>
<style scoped>
.news_date {
    padding-left: 0.3rem;
    background: #ffc674;
    height: 0.59rem;
    color: #fff;
    font-size: 0.28rem;
    line-height: 0.59rem;
}

.listWidth .yd-list-item .span_logo:after {
    width: 0.8rem;
    height: 0.4rem;
    content: '';
    position: absolute;
    top: 0;
    right: 0.6rem;
    background: url(../../../static/images/trs_yl_02.png) no-repeat;
    background-position: 0.4rem 0.05rem;
    background-size: 0.4rem;
    overflow: hidden;
}

.span_title {
    font-size: 0.3rem;
    display: block;
    width: 5.7rem;
    height: 0.45rem;
    overflow: hidden;
    line-height: 0.45rem;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.title {
    overflow: hidden;
    width: 5.5rem;
    height: 0.45rem;
    line-height: 0.45rem;
    font-size: 0.3rem;
    display: block;
}

.span_publisher {
    font-size: 0.26rem;
    width: 3rem;
    display: block;
    float: left;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}

.span_date {
    font-size: 0.26rem;
    padding-left: 0.8rem;
    padding-right: 0.32rem;
}

.span_time {
    font-size: 0.26rem;
}

.read_btn {
    width: 1.04rem;
    height: 0.44rem;
    background: url(../../../static/images/trs_yl_0921.png) #ffa740;
    background-repeat: no-repeat;
    background-size: 0.26rem;
    background-position: 0.06rem 0.06rem;
    border-radius: .5rem;
    position: fixed;
    bottom: 0.3rem;
    right: 0.5rem;
    z-index: 5;
}

.read_btn02 {
    background: url(../../../static/images/trs_yl_0921.png) #4aa9f6;
    background-repeat: no-repeat;
    background-size: 0.26rem;
    background-position: 0.06rem 0.06rem;
}

.read_btn .read_btn_a {
    color: #fff;
    font-size: .22rem;
    line-height: .44rem;
    padding-left: 0.4rem;
    width: 1.04rem;
    height: 0.44rem;
    display: block;
}

.titleColor {
    color: #999;
}

.fontColor {
    color: #999;
}
</style>


